import { useTranslation } from "react-i18next"
import { Fragment, useContext } from "react"
import Spinner from "@components/spinner/Loading-spinner"
import { CardText, Alert } from "reactstrap"
import { Skeleton } from "@mui/material"
import { ThemeColors } from "@src/utility/context/ThemeColors"

import noDataDisplayImg from "@src/assets/images/pages/404.png"
import errorDataDisplayImg from "@src/assets/images/pages/500.png"

export const NoDataDisplay = ({ noImage = false, dataName, isFromAddPage }) => {
  const { t } = useTranslation()

  let messageText = !isFromAddPage
    ? t(
        "There are no records to display. Try changing the filter or adding new data"
      )
    : t("Add one or more data here...")

  return (
    <div
      className="align-items-center w-100"
      style={{ padding: "24px", textAlign: "center" }}
    >
      {!noImage && (
        <img
          style={{ maxWidth: "50%", minWidth: "35%" }}
          className="mb-1 no-data-grid"
          src={noDataDisplayImg}
        />
      )}
      <h6
        style={{
          color: "rgb(136 135 137)"
        }}
      >
        {!dataName
          ? messageText
          : messageText
              .replaceAll("data", `data ${t(dataName)}`)
              .replaceAll("records", `${t(dataName)} records`)}
      </h6>
      {/* <Alert color="info">
        <h4 className="alert-heading">{t("No Records Find")}</h4>
        <div className="alert-body">
          {t(
            "There are no records to display. Try changing the filter or adding new data"
          )}
        </div>
      </Alert> */}
    </div>
  )
}

export const ErrorFetchData = ({ message }) => {
  const { t } = useTranslation()
  return (
    <div
      className="align-items-center w-100"
      style={{ padding: "24px", textAlign: "center" }}
    >
      <img className="mt-2 mb-1" src={errorDataDisplayImg} />
      <Alert className="mt-2" color="danger">
        <h4 className="alert-heading">{t("Error")}</h4>
        <div className="alert-body">
          {message !== undefined
            ? t(message)
            : t("An error has occurred in fetching data.")}
        </div>
      </Alert>
    </div>
  )
}

export const Loader = ({ loadingText, dataName }) => {
  const { t } = useTranslation()
  if (!loadingText) {
    loadingText = t("Please Wait")
  }
  if (dataName) {
    loadingText = `${t(`Loading`)} ${t(dataName)} Data`
  }
  return (
    <Fragment>
      <Spinner />
      <CardText className="mb-0 mt-1">{loadingText}...</CardText>
    </Fragment>
  )
}

export const TableSkeletonLoading = ({ column }) => {
  const { colors } = useContext(ThemeColors)
  return column.name === "Actions" ? (
    <Fragment>
      <Skeleton
        // sx={{ bgcolor: colors.dark.light }}
        animation="wave"
        className="ms-2 me-1"
        width={30}
        height={40}
      />
      <Skeleton
        // sx={{ bgcolor: colors.dark.light }}
        animation="wave"
        width={30}
        height={40}
      />
    </Fragment>
  ) : (
    <Skeleton
      // sx={{ bgcolor: colors.dark.light }}
      animation="wave"
      width="100%"
      height={40}
    />
  )
}
